/**
 * Hero Module SCSS
 * Stili per il modulo hero jj
 */
.hero-module {
  position: relative;
  min-height: var(--hero-height, 100vh);
  display: flex;
  align-items: center;
  overflow: hidden;
  background: var(--gradient-dark);
}

.hero-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  z-index: 1;
  opacity: 0.3;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--gradient-hero);
  opacity: 0.8;
  z-index: 2;
}

.hero-container {
  position: relative;
  z-index: 3;
  width: 100%;
  max-width: var(--container-xl);
  margin: 0 auto;
  padding: var(--space-lg);
}

.hero-content {
  display: flex;
  align-items: center;
  min-height: 100vh;
}

.hero-text {
  color: var(--text-primary);
  max-width: 600px;
}

.hero-date-tag {
  display: inline-flex;
  align-items: center;
  gap: var(--space-sm);
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border: 1px solid var(--bg-glass-hover);
  border-radius: var(--radius-full);
  padding: var(--space-sm) var(--space-md);
  margin-bottom: var(--space-lg);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-semibold);
  color: var(--text-primary);
}

.hero-title {
  font-family: var(--font-display);
  font-size: var(--font-size-8xl);
  font-weight: var(--font-weight-black);
  margin-bottom: var(--space-lg);
  line-height: var(--line-height-tight);
  color: var(--text-primary);
  text-transform: uppercase;
}

.hero-subtitle {
  font-family: var(--font-accent);
  font-size: var(--font-size-3xl);
  margin-bottom: var(--space-lg);
  color: var(--accent-cyan);
  font-weight: var(--font-weight-light);
}

.hero-description {
  font-size: var(--font-size-lg);
  color: var(--text-secondary);
  line-height: var(--line-height-relaxed);
  margin-bottom: var(--space-2xl);
}

.hero-actions {
  display: flex;
  gap: var(--space-md);
  flex-wrap: wrap;
}

@media (max-width: 768px) {
  .hero-title {
    font-size: var(--font-size-4xl);
  }
  .hero-subtitle {
    font-size: var(--font-size-xl);
  }
  .hero-actions {
    justify-content: center;
  }
}
@media (max-width: 480px) {
  .hero-title {
    font-size: var(--font-size-3xl);
  }
  .hero-subtitle {
    font-size: var(--font-size-lg);
  }
  .hero-actions {
    flex-direction: column;
    align-items: center;
  }
}
/**
 * Menu Module SCSS
 * Stili per il modulo menu
 */
.main-menu {
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--bg-glass-hover);
  box-shadow: var(--shadow-lg);
  transition: all var(--transition-fast);
}

.main-menu.sticky {
  position: sticky;
  top: 0;
  z-index: var(--z-sticky, 1020);
}

.menu-container {
  max-width: var(--container-xl);
  margin: 0 auto;
  padding: var(--space-md) var(--space-lg);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu-brand .brand-link {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.brand-logo {
  height: 40px;
  width: auto;
  transition: transform var(--transition-fast);
}

.brand-logo:hover {
  transform: scale(1.05);
}

.menu-nav {
  display: flex;
  list-style: none;
  gap: var(--space-lg);
  margin: 0;
  padding: 0;
}

.menu-link {
  color: var(--text-primary);
  text-decoration: none;
  font-weight: var(--font-weight-medium);
  padding: var(--space-sm) var(--space-md);
  border-radius: var(--radius-md);
  transition: all var(--transition-fast);
  position: relative;
}

.menu-link::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 50%;
  width: 0;
  height: 2px;
  background: var(--primary);
  transition: all var(--transition-fast);
  transform: translateX(-50%);
}

.menu-link:hover::after {
  width: 80%;
}

.menu-toggle {
  display: none;
  flex-direction: column;
  cursor: pointer;
  gap: 4px;
  padding: var(--space-sm);
}

.menu-toggle span {
  width: 25px;
  height: 3px;
  background: var(--secondary);
  transition: var(--transition-fast);
  border-radius: 2px;
}

.menu-toggle.active span:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}

.menu-toggle.active span:nth-child(2) {
  opacity: 0;
}

.menu-toggle.active span:nth-child(3) {
  transform: rotate(-45deg) translate(7px, -6px);
}

@media (max-width: 768px) {
  .menu-toggle {
    display: flex;
  }
  .menu-nav {
    position: fixed;
    top: 100%;
    left: 0;
    width: 100%;
    background: var(--bg-glass);
    backdrop-filter: blur(10px);
    flex-direction: column;
    padding: var(--space-lg);
    box-shadow: var(--shadow-lg);
    transform: translateY(-100%);
    opacity: 0;
    visibility: hidden;
    transition: all var(--transition-normal);
    z-index: var(--z-dropdown, 1000);
  }
  .menu-nav.active {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
  }
  .menu-nav li {
    width: 100%;
  }
  .menu-link {
    display: block;
    padding: var(--space-md);
    text-align: center;
    border-bottom: 1px solid var(--bg-glass-hover);
  }
  .menu-link:last-child {
    border-bottom: none;
  }
  .menu-link::after {
    display: none;
  }
}
@media (max-width: 480px) {
  .menu-container {
    padding: var(--space-sm) var(--space-md);
  }
  .brand-logo {
    height: 35px;
  }
}
/**
 * Race Cards Module SCSS
 * Stili per il modulo race cards - Design completo e definito
 */
.race-cards-module {
  padding: var(--space-3xl) 0;
  background: var(--section-bg-1);
}

.race-cards-container {
  max-width: var(--container-xl);
  margin: 0 auto;
  padding: 0 var(--space-lg);
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: var(--space-xl);
}

.race-card {
  position: relative;
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  border: 1px solid var(--bg-glass-hover);
  border-radius: var(--radius-xl);
  padding: var(--space-xl);
  transition: all var(--transition-normal);
  overflow: hidden;
}

.race-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-xl);
  border-color: var(--primary);
}

.card-number {
  position: absolute;
  top: var(--space-lg);
  right: var(--space-lg);
  font-family: var(--font-display);
  font-size: var(--font-size-9xl);
  font-weight: var(--font-weight-black);
  opacity: 0.1;
  color: var(--primary);
  z-index: 1;
}

.marathon-card .card-number {
  color: var(--marathon-blue);
}

.portici-card .card-number {
  color: var(--portici-pink);
}

.runtune-card .card-number {
  color: var(--runtune-green);
}

.card-tag {
  position: absolute;
  top: var(--space-lg);
  left: var(--space-lg);
  padding: var(--space-sm) var(--space-md);
  border-radius: var(--radius-full);
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-bold);
  text-transform: uppercase;
  z-index: 2;
}

.marathon-tag {
  background: var(--marathon-blue);
  color: var(--text-primary);
}

.portici-tag {
  background: var(--portici-pink);
  color: var(--text-primary);
}

.runtune-tag {
  background: var(--runtune-green);
  color: var(--text-dark);
}

.card-content {
  position: relative;
  z-index: 3;
  margin-top: var(--space-2xl);
}

.card-title {
  font-family: var(--font-display);
  font-size: var(--font-size-4xl);
  font-weight: var(--font-weight-black);
  color: var(--text-primary);
  margin: 0 0 var(--space-md) 0;
  text-transform: uppercase;
}

.card-distance {
  font-family: var(--font-display);
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-bold);
  margin-bottom: var(--space-lg);
}

.marathon-distance {
  color: var(--marathon-blue);
}

.portici-distance {
  color: var(--portici-pink);
}

.runtune-distance {
  color: var(--runtune-green);
}

.card-description {
  color: var(--text-secondary);
  line-height: var(--line-height-relaxed);
  margin-bottom: var(--space-xl);
}

.card-details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: var(--space-md);
  margin-bottom: var(--space-xl);
}

.detail-item {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  color: var(--text-secondary);
  font-size: var(--font-size-sm);
}

.detail-item i {
  width: 16px;
  color: var(--primary);
}

.card-button {
  width: 100%;
  padding: var(--space-md) var(--space-lg);
  border: none;
  border-radius: var(--radius-lg);
  font-family: var(--font-primary);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-bold);
  text-transform: uppercase;
  cursor: pointer;
  transition: all var(--transition-normal);
}

.marathon-button {
  background: var(--marathon-blue);
  color: var(--text-primary);
}

.marathon-button:hover {
  background: var(--hover-blue);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(35, 168, 235, 0.3);
}

.portici-button {
  background: var(--portici-pink);
  color: var(--text-primary);
}

.portici-button:hover {
  background: var(--hover-portici-pink);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(220, 51, 94, 0.3);
}

.runtune-button {
  background: var(--runtune-green);
  color: var(--text-dark);
}

.runtune-button:hover {
  background: var(--hover-runtune-green);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(203, 223, 68, 0.3);
}

/* Responsive */
@media (max-width: 768px) {
  .race-cards-container {
    grid-template-columns: 1fr;
    padding: 0 var(--space-md);
  }
  .card-number {
    font-size: var(--font-size-7xl);
  }
  .card-title {
    font-size: var(--font-size-3xl);
  }
  .card-distance {
    font-size: var(--font-size-xl);
  }
}
@media (max-width: 480px) {
  .race-card {
    padding: var(--space-lg);
  }
  .card-number {
    font-size: var(--font-size-6xl);
  }
  .card-title {
    font-size: var(--font-size-2xl);
  }
  .card-details {
    grid-template-columns: 1fr;
  }
}
/**
 * Results Module SCSS
 * Stili per il modulo results
 */
.results-module {
  padding: var(--space-3xl) 0;
  background: var(--section-bg-2);
}

.results-container {
  max-width: var(--container-xl);
  margin: 0 auto;
  padding: 0 var(--space-lg);
}

.results-header {
  text-align: center;
  margin-bottom: var(--space-2xl);
}

.results-title {
  font-family: var(--font-display);
  font-size: var(--font-size-4xl);
  font-weight: var(--font-weight-black);
  color: var(--secondary);
  margin: 0;
  text-shadow: 0 0 10px rgba(0, 188, 212, 0.3);
}

.results-filters {
  display: flex;
  justify-content: center;
  gap: var(--space-md);
  margin-bottom: var(--space-xl);
  flex-wrap: wrap;
}

.filter-select {
  padding: var(--space-sm) var(--space-md);
  border: 1px solid var(--bg-glass-hover);
  border-radius: var(--radius-lg);
  background: var(--bg-glass);
  color: var(--text-primary);
  font-size: var(--font-size-sm);
  transition: all var(--transition-fast);
  backdrop-filter: blur(10px);
}
.filter-select:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(35, 168, 235, 0.2);
}

.results-table {
  background: var(--bg-glass);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-lg);
  overflow: hidden;
  border: 1px solid var(--bg-glass-hover);
  backdrop-filter: blur(10px);
}

.results-table-content {
  width: 100%;
  border-collapse: collapse;
  font-size: var(--font-size-sm);
  color: var(--text-primary);
}
.results-table-content thead {
  background: var(--gradient-primary);
  color: var(--text-primary);
}
.results-table-content thead th {
  padding: var(--space-md);
  text-align: left;
  font-weight: var(--font-weight-semibold);
  border-bottom: 1px solid var(--bg-glass-hover);
}
.results-table-content tbody tr {
  border-bottom: 1px solid var(--bg-glass-hover);
  transition: all var(--transition-fast);
}
.results-table-content tbody tr:nth-child(even) {
  background: var(--bg-glass);
}
.results-table-content tbody tr:hover {
  background: var(--bg-glass-hover);
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
.results-table-content tbody tr td {
  padding: var(--space-md);
  vertical-align: middle;
}

.rank {
  font-weight: var(--font-weight-bold);
  color: var(--primary);
  text-align: center;
  width: 60px;
}

.name {
  font-weight: var(--font-weight-semibold);
}

.time {
  font-family: "Courier New", monospace;
  color: var(--accent-cyan);
}

.category {
  background: var(--bg-glass);
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-sm);
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-medium);
}

@media (max-width: 768px) {
  .results-table-content {
    font-size: var(--font-size-xs);
  }
  .results-table-content thead th,
  .results-table-content tbody td {
    padding: var(--space-sm);
  }
  .results-filters {
    flex-direction: column;
    align-items: center;
  }
  .filter-select {
    width: 100%;
    max-width: 300px;
  }
}
@media (max-width: 480px) {
  .results-table-content {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }
}
/**
 * Footer Module SCSS
 * Stili per il modulo footer
 */
.site-footer {
  background: var(--section-bg-1);
  color: var(--text-primary);
  margin-top: var(--space-3xl);
  position: relative;
  overflow: hidden;
}

.footer-container {
  max-width: var(--container-xl);
  margin: 0 auto;
  padding: var(--space-3xl) var(--space-lg);
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--space-2xl);
  margin-bottom: var(--space-2xl);
}

.footer-section h3, .footer-section h4 {
  margin-bottom: var(--space-lg);
}

.footer-title {
  font-family: var(--font-display);
  font-size: var(--font-size-3xl);
  color: var(--secondary);
  margin: 0;
  text-shadow: 0 0 10px rgba(0, 188, 212, 0.3);
}

.footer-subtitle {
  font-size: var(--font-size-lg);
  color: var(--text-primary);
  margin-bottom: var(--space-md);
}

.footer-description {
  color: var(--text-secondary);
  line-height: var(--line-height-relaxed);
  margin-bottom: var(--space-lg);
}

.social-links {
  display: flex;
  gap: var(--space-md);
  flex-wrap: wrap;
}

.social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: var(--bg-glass);
  color: var(--secondary);
  border-radius: var(--radius-full);
  transition: all var(--transition-fast);
  backdrop-filter: blur(10px);
  border: 1px solid var(--bg-glass-hover);
}
.social-link:hover {
  background: var(--secondary);
  color: var(--text-primary);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(0, 188, 212, 0.3);
}

.footer-links {
  list-style: none;
}
.footer-links li {
  margin-bottom: var(--space-sm);
}
.footer-links li a {
  color: var(--text-secondary);
  transition: color var(--transition-fast);
}
.footer-links li a:hover {
  color: var(--primary);
}

.contact-info p {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  margin-bottom: var(--space-sm);
  color: var(--text-secondary);
}
.contact-info p i {
  color: var(--primary);
  width: 16px;
}

.newsletter .newsletter-form {
  display: flex;
  gap: var(--space-sm);
  margin-top: var(--space-md);
}
.newsletter .newsletter-input {
  flex: 1;
  padding: var(--space-sm) var(--space-md);
  border: 1px solid var(--bg-glass-hover);
  border-radius: var(--radius-md);
  background: var(--bg-glass);
  color: var(--text-primary);
  backdrop-filter: blur(10px);
}
.newsletter .newsletter-input:focus {
  outline: none;
  border-color: var(--secondary);
  box-shadow: 0 0 0 3px rgba(0, 188, 212, 0.2);
}
.newsletter .newsletter-input::placeholder {
  color: var(--text-muted);
}
.newsletter .newsletter-btn {
  padding: var(--space-sm) var(--space-lg);
  background: var(--gradient-button);
  color: var(--text-primary);
  border: none;
  border-radius: var(--radius-md);
  font-weight: var(--font-weight-semibold);
  cursor: pointer;
  transition: all var(--transition-fast);
  box-shadow: 0 4px 12px rgba(0, 188, 212, 0.3);
}
.newsletter .newsletter-btn:hover {
  background: var(--gradient-button-hover);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(0, 188, 212, 0.4);
}

.footer-bottom {
  border-top: 1px solid var(--bg-glass-hover);
  padding-top: var(--space-lg);
  text-align: center;
}

.copyright {
  color: var(--text-muted);
  font-size: var(--font-size-sm);
  margin: 0;
}

@media (max-width: 768px) {
  .footer-content {
    grid-template-columns: 1fr;
    gap: var(--space-lg);
  }
  .newsletter-form {
    flex-direction: column;
  }
  .social-links {
    justify-content: center;
  }
}
@media (max-width: 480px) {
  .footer-container {
    padding: var(--space-xl) var(--space-md);
  }
  .footer-title {
    font-size: var(--font-size-2xl);
  }
}
/**
 * Rich Text Module SCSS
 */
.rich-text-module {
  background: var(--bg-primary);
  padding: var(--space-3xl) 0;
}

.rich-text-content {
  max-width: var(--container-xl);
  margin: 0 auto;
  padding: 0 var(--space-lg);
}

.rich-text-content h1,
.rich-text-content h2,
.rich-text-content h3,
.rich-text-content h4,
.rich-text-content h5,
.rich-text-content h6 {
  color: var(--secondary);
  margin-bottom: var(--space-lg);
  margin-top: var(--space-xl);
}

.rich-text-content h1:first-child,
.rich-text-content h2:first-child,
.rich-text-content h3:first-child {
  margin-top: 0;
}

.rich-text-content p {
  font-size: var(--font-size-lg);
  line-height: var(--line-height-relaxed);
  color: var(--text-secondary);
  margin-bottom: var(--space-lg);
}

.rich-text-content p:last-child {
  margin-bottom: 0;
}

.rich-text-content ul,
.rich-text-content ol {
  margin-bottom: var(--space-lg);
  padding-left: var(--space-xl);
}

.rich-text-content li {
  margin-bottom: var(--space-sm);
  line-height: var(--line-height-normal);
}

.rich-text-content a {
  color: var(--primary);
  text-decoration: none;
  font-weight: var(--font-weight-medium);
  transition: color var(--transition-fast);
}

.rich-text-content a:hover {
  color: var(--hover-blue);
  text-decoration: underline;
}

.rich-text-content strong {
  font-weight: var(--font-weight-bold);
  color: var(--secondary);
}

.rich-text-content em {
  font-style: italic;
  color: var(--text-muted);
}

.rich-text-content blockquote {
  border-left: 4px solid var(--primary);
  padding-left: var(--space-lg);
  margin: var(--space-xl) 0;
  font-style: italic;
  color: var(--text-muted);
  background: var(--bg-glass);
  padding: var(--space-lg);
  border-radius: var(--radius-md);
}

.rich-text-content img {
  max-width: 100%;
  height: auto;
  border-radius: var(--radius-md);
  margin: var(--space-lg) 0;
  box-shadow: var(--shadow-md);
}

@media (max-width: 768px) {
  .rich-text-content {
    padding: 0 var(--space-md);
  }
  .rich-text-content p {
    font-size: var(--font-size-base);
  }
  .rich-text-content ul,
  .rich-text-content ol {
    padding-left: var(--space-lg);
  }
}
@media (max-width: 480px) {
  .rich-text-module {
    padding: var(--space-xl) 0;
  }
  .rich-text-content {
    padding: 0 var(--space-sm);
  }
}
/**
 * Button Module SCSS
 * Sistema modulare per pulsanti riutilizzabili
 */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-sm);
  padding: var(--space-sm) var(--space-md);
  border: none;
  border-radius: var(--radius-lg);
  font-family: var(--font-primary);
  font-weight: var(--font-weight-semibold);
  font-size: var(--font-size-base);
  line-height: 1;
  text-decoration: none;
  cursor: pointer;
  transition: all var(--transition-normal);
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
.btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left var(--transition-slow);
}
.btn:hover::before {
  left: 100%;
}
.btn:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(93, 173, 226, 0.3);
}
.btn.btn-disabled, .btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  pointer-events: none;
}
.btn.btn-loading {
  pointer-events: none;
}
.btn.btn-loading .btn-text {
  opacity: 0.7;
}
.btn.btn-full-width {
  width: 100%;
}

.btn-spinner {
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 2px solid transparent;
  border-top: 2px solid currentColor;
  border-radius: 50%;
  animation: btn-spin 1s linear infinite;
}

@keyframes btn-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.btn-primary {
  background: var(--gradient-primary);
  color: var(--text-primary);
  box-shadow: var(--shadow-glow);
}
.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-glow), var(--shadow-lg);
}
.btn-primary:active {
  transform: translateY(0);
}

.btn-secondary {
  background: var(--gradient-button);
  color: var(--text-primary);
  box-shadow: var(--shadow-glow);
}
.btn-secondary:hover {
  background: var(--gradient-button-hover);
  transform: translateY(-2px);
  box-shadow: var(--shadow-glow), var(--shadow-lg);
}

.btn-marathon {
  background: var(--primary);
  color: var(--text-primary);
  box-shadow: 0 0 20px rgba(35, 168, 235, 0.3);
}
.btn-marathon:hover {
  background: var(--hover-blue);
  transform: translateY(-2px);
  box-shadow: 0 0 30px rgba(35, 168, 235, 0.4);
}

.btn-portici {
  background: var(--portici-pink);
  color: var(--text-primary);
  box-shadow: 0 0 20px rgba(220, 51, 94, 0.3);
}
.btn-portici:hover {
  background: var(--hover-portici-pink);
  transform: translateY(-2px);
  box-shadow: 0 0 30px rgba(220, 51, 94, 0.4);
}

.btn-runtune {
  background: var(--runtune-green);
  color: var(--text-dark);
  box-shadow: 0 0 20px rgba(203, 223, 68, 0.3);
}
.btn-runtune:hover {
  background: var(--hover-runtune-green);
  transform: translateY(-2px);
  box-shadow: 0 0 30px rgba(203, 223, 68, 0.4);
}

.btn-ghost {
  background: transparent;
  color: var(--text-primary);
  border: 1px solid var(--bg-glass-hover);
  backdrop-filter: blur(10px);
}
.btn-ghost:hover {
  background: var(--bg-glass-hover);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.btn-outline {
  background: transparent;
  color: var(--primary);
  border: 2px solid var(--primary);
}
.btn-outline:hover {
  background: var(--primary);
  color: var(--text-primary);
  transform: translateY(-2px);
  box-shadow: var(--shadow-glow);
}

.btn-small {
  padding: var(--space-xs) var(--space-sm);
  font-size: var(--font-size-sm);
  border-radius: var(--radius-sm);
}

.btn-medium {
  padding: var(--space-sm) var(--space-md);
  font-size: var(--font-size-base);
  border-radius: var(--radius-lg);
}

.btn-large {
  padding: var(--space-md) var(--space-xl);
  font-size: var(--font-size-lg);
  border-radius: var(--radius-lg);
}
.btn-large .btn-spinner {
  width: 20px;
  height: 20px;
}

.btn-xl {
  padding: var(--space-lg) var(--space-2xl);
  font-size: var(--font-size-xl);
  border-radius: var(--radius-xl);
}
.btn-xl .btn-spinner {
  width: 24px;
  height: 24px;
}

@media (max-width: 768px) {
  .btn {
    font-size: var(--font-size-sm);
    padding: var(--space-xs) var(--space-sm);
  }
  .btn-large {
    padding: var(--space-sm) var(--space-md);
    font-size: var(--font-size-base);
  }
  .btn-xl {
    padding: var(--space-md) var(--space-lg);
    font-size: var(--font-size-lg);
  }
}
.btn-bounce {
  animation: btn-bounce 0.6s ease;
}

@keyframes btn-bounce {
  0%, 20%, 53%, 80%, 100% {
    transform: translateY(0);
  }
  40%, 43% {
    transform: translateY(-10px);
  }
  70% {
    transform: translateY(-5px);
  }
  90% {
    transform: translateY(-2px);
  }
}
.btn-pulse {
  animation: btn-pulse 2s infinite;
}

@keyframes btn-pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(93, 173, 226, 0.7);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(93, 173, 226, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(93, 173, 226, 0);
  }
}
.bm-select .bm-select__input {
  min-width: 220px;
}

.select2-container .select2-selection--single,
.select2-container .select2-selection--multiple {
  border-color: var(--secondary);
}

.select2-container--default .select2-selection--single .select2-selection__placeholder {
  color: var(--text-secondary);
}

.site-wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.site-main {
  flex: 1;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 6px;
  background: var(--primary);
  color: var(--text-primary);
  padding: 8px;
  text-decoration: none;
  border-radius: 4px;
  z-index: 1000;
}
.skip-link:focus {
  top: 6px;
}